const DEF_WIDTH = 640;   // default canvas dimension, unit in px
const DEF_HEIGHT = 480;
const DEF_BORDER_COLOR = '#333';
const DEF_BG_COLOR = '#ccc';

export default class {
    constructor(options) {
        this.snake = options.snake;
        this.width = options.width || DEF_WIDTH;
        this.height = options.height || DEF_HEIGHT;
        this.borderColor = options.borderColor || DEF_BORDER_COLOR;
        this.bgColor = options.bgColor || DEF_BG_COLOR;
        this.initCanvas();
    }

    initCanvas() {
        this.canvas = document.getElementById('canvas');
        this.ctx = this.canvas.getContext('2d');

        this.canvas.width = this.width;
        this.canvas.height = this.height;

        // draw background and border
        this.ctx.rect(0, 0, this.width, this.height);
        this.ctx.fillStyle = this.bgColor;
        this.ctx.fill();

        this.ctx.strokeStyle = this.borderColor;
        this.ctx.lineWidth = 2;
        this.ctx.stroke();

    }

    drawSnake() {
        this.ctx.strokeStyle = this.snake.borderColor;
        this.ctx.lineWidth = 1;
        this.ctx.beginPath();

        for (let i = 0; i < this.snake.getLength(); i++) {
            let block = this.snake.getBodyBlock(i);
            this.ctx.fillRect(10, 10, this.snake.size, this.snake.size);
        }

        this.ctx.stroke();
        this.ctx.closePath();
    }
}



